import "./import/modules";
import "./import/components";



var textarea = document.querySelector('.contact-us__textarea');

textarea.addEventListener('keyup', function(){
  if(this.scrollTop > 0){
    this.style.height = this.scrollHeight + "px";
  }
});



var attachFileButton = document.querySelector(".contact-us__attach-file");
var deleteFileButton = document.querySelector(".contact-us__delete-file");
var fileName = document.querySelector(".contact-us__file-name");

attachFileButton.addEventListener("change", attachFile, false);
deleteFileButton.addEventListener("click", deleteFile, false);

function attachFile() {
  fileName.innerHTML = this.files[0].name;
  fileName.style.display = 'block';
  deleteFileButton.style.display = 'block';
}
function deleteFile() {
  attachFileButton.value = '';
  fileName.innerHTML = '';
  fileName.style.display = 'none';
  deleteFileButton.style.display = 'none';
}